import React, {Component} from "react"
import mapboxgl from 'mapbox-gl'
import 'mapbox-gl/dist/mapbox-gl.css';
import street_cars from '../assets/test2.json'

mapboxgl.accessToken = 'pk.eyJ1Ijoic2FhZGlxbSIsImEiOiJjamJpMXcxa3AyMG9zMzNyNmdxNDlneGRvIn0.wjlI8r1S_-xxtq2d-W5qPA';

class GtfsMap extends Component {

  constructor(props) {
    super(props);
    this.state = {
      lng: -114.0708,
      lat: 51.0486,
      zoom: 10.2,
    };
  }

  componentDidMount() {
    const {lng, lat, zoom } = this.state;
    this.map = new mapboxgl.Map({
      container: this.mapContainer,
      style: 'mapbox://styles/saadiqm/cjfnjiowo0zj62rpj0y1qpib0',
      center: [lng, lat],
      zoom
    });

    this.map.on('load', () => {

      this.map.addSource("Street Cars", {
          type: 'geojson',
          data: street_cars
        });

      this.map.addLayer({
        "id": "Street Cars",
        "type": "line",
        "source": 'Street Cars',
        "paint": {
            "line-color": "red",
            "line-width": 8,
            "line-opacity": 0.7,
            "line-offset": [
              'match',
              ['get', 'short_name'],
              "2",-1,
              "3",-1*2,
              "5",-1*3,
              "6",1,
              "7",2.5*0,
              "9",0,
              0
            ]
        },
        "layout": {
            "line-cap": "round",
            "line-join":"round"
        },

      });
    });


  }

  render(){

    return(
        <div ref={el => this.mapContainer = el} style={{position: 'absolute',
          top: 0,
          bottom: 0,
          width: '100%',
          height: '100%'}} />
    );
  }
}

export default GtfsMap
